/* *************************
Styling for Contact Us page
* ************************ */
.contact--us--container {
  background-color: #edeae8;
  height: 92vh;
  padding: 2rem 1rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 70px 0;
  justify-items: center;
  align-content: flex-start;
}
.contact__us__heading {
  font-size: 2.25rem;
  font-family: var(--secondary-font);
  color: var(--h1-color);
  text-decoration: underline 3px currentColor;
  text-underline-offset: 2px;
}
.contact__cards__container {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
}
.contact__card {
  background-color: var(--color-white);
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  transform: skewX(-20deg);
  transition: box-shadow 250ms ease-out, transform 350ms ease-out;
}
.contact__card:hover {
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);
  transform: skewX(-20deg) scale(1.1);
}
.contact__card a {
  padding: 2rem 1.5rem;
  text-decoration: none;
  display: inline-block;
  transform: skewX(20deg);
}
.contact__card a:hover, .contact__card a:focus {
  opacity: 0.7;
}

.contact__card a:first-child {
  font-size: 1.25rem;
  font-weight: 500;
  font-family: var(--secondary-font);
  color: var(--common-color);
}

.contact__number,
.email__address {
  font-family: var(--tertiary-font);
  font-weight: 500;
  letter-spacing: 0.75px;
  color: var(--common-color);
}

/* ****************************
MEDIA QUERIES
***************************** */
@media screen and (max-width: 540px) {
  .contact__card:hover {
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.15);
    transform: skew(-20deg);
  }

  .contact__card a {
    padding: 2rem 1rem;
  }
}